import { useEffect } from "react";

useEffect(() => {
    const fetchGaming = async () => {
        try {
        const res = await fetch(
            `https://api.rawg.io/api/genres?key=${process.env.NEXT_PUBLIC_RAWG_API_KEY}` // Note: NEXT_PUBLIC_ prefix
        );
        
        if (!res.ok) {
            throw new Error('Failed to fetch data');
        }
        
        const data = await res.json();
        console.log('API Response:', data); // Debug log
        setGenres(data.results || []); // RAWG API returns data in 'results' array
        setLoading(false);
        } catch (err) {
        setError('Failed to fetch genres');
        setLoading(false);
        }
    };

    fetchGaming();
    }, []);